.default-banner-container {
    height: 300px;
    background-image: url("https://a.optmnstr.com/users/b087f2df60dd/images/76226086f74c1550256078-shutterstock-blog.jpg");
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    color: white;
    text-shadow: 0px 2px 6px #292929;
    font-size: 40px;
    text-transform: uppercase;
}

@media only screen and (max-width: 768px){
    .default-banner-container {
        height: 200px;
    }
}