.login-page-container {
    width: 100%;
    height: calc(100vh - 64px);
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url("https://a.optmnstr.com/users/b087f2df60dd/images/76226086f74c1550256078-shutterstock-blog.jpg");
}

.login-form-container {
    width: 300px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 2px 6px rgba(50, 50, 50, 0.3);
}

.login-form {
    padding: 10px;
    height: 200px;
}

.login-failed-alert {
    color: red;
}

.create-account {
    padding: 10px;
    border-radius: 0 0 10px 10px;
    border-top: solid grey 1px;
    background-color: lightgrey;
    color: grey;
}