.cart-record-container {
    width: 100%;
    padding: 10px 30px;
    display: flex;
    box-sizing: border-box;
    justify-content: space-between;
}

.cart-record-image {
    height: 80px;
    width: 60px;
    background-color: grey;
}

.cart-record-information {
    text-align: left;
}

.cart-record-title {
    font-weight: bold;
}