.back-drop-container {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    bottom:0;
    left: 0;
    right: 0;
    z-index: 100;
    visibility: hidden;
}

#back-drop-container {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    bottom:0;
    left: 0;
    right: 0;
}

#back-drop-container.active {
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 100;
    transition: background-color 400ms linear;
}

#back-drop-container.inactive {
    background-color: rgba(0, 0, 0, 0);
    z-index: -100;
    transition: background-color 400ms linear;
    transition-delay: z-index 3s;
}