.page-cart-record-container {
    width: 100%;
    padding: 10px 30px;
    position: relative;
    display: flex;
    box-sizing: border-box;
    justify-content: space-between;
    background-color: #fff;
}

.page-cart-record-container::after {
    content: "";
    height: 1px;
    position: absolute;
    background-color: lightgrey;
    bottom: 0;
    left: 20px;
    right: 20px;
}

.page-cart-record-image {
    height: 80px;
    width: 60px;
    background-color: grey;
}

.page-cart-record-information {
    text-align: left;
}

.page-cart-record-title {
    font-weight: bold;
}

