.order-complete-page-container {
    background:linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url("https://a.optmnstr.com/users/b087f2df60dd/images/76226086f74c1550256078-shutterstock-blog.jpg") no-repeat bottom center; /*https://i.imgur.com/5lmwwtU.gif*/
    background-size: cover;
    display: flex;
    justify-content: space-around;
    min-height: 80vh;
}

.order-complete-page-message {
    background: white;
    width: 600px;
    max-height: 300px;
    padding: 20px 20px 40px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-top: 20px;
}

.complete-header {
    font-size: 40px;
    font-weight: bold;
}

.groen {
    color:green;
}

.oranje {
    color: orange;
}

