.shop-page-container {
    min-height: 90vh;
}

.stock-container {
    width: 1400px;
    max-width: 100%;
    margin: 0 auto;
    padding: 60px 2%;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-column-gap: 3%;
    grid-row-gap: 40px;
}

@media only screen and (max-width: 1025px) {
    .stock-container {
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media only screen and (max-width: 768px) {
    .stock-container {
        grid-template-columns: 1fr 1fr;
        padding: 40px 2%;
        grid-row-gap: 20px;
    }
}