#navbar {
    width: 65vw;
    transform: translateX(-65vw);
    transition: transform 400ms ease-in-out;

    position: fixed;
    top:0;
    bottom: 0;
    width: 65vw;
    background-color: white;
    
    z-index: 200;
}

#navbar > ul {
    display: flex;
    flex-direction: column;
}

#navbar a {
    display: block;
    text-decoration: none;
    color: inherit;
    padding: 20px;
}

#navbar.slide-in {
    transform: translateX(0);
    box-shadow: 2px 0px 6px rgb(0, 0, 0, 0.5);
}

#navbar.slide-out {
    transform: translateX(-65vw);
}