.top-nav-container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    min-height: 64px;
    display: flex;
    background-color: #fff;
    box-shadow: 0px 2px 6px rgba(50, 50, 50, 0.3);
}

.top-nav-container a {
    text-decoration: none;
    color: inherit;
}

.top-nav-inner-container {
    width: 1400px;
    max-width: 100%;
    margin: 0 auto;
    padding: 0 1%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.hamburger-container {
    display: none;
}

.icon {
    width: 100%;
}

.logo-container {
    height: 46px;
    margin: 0 10px;
    font-family: 'Catamaran', sans-serif;
    font-size: 32px;
    font-weight: 900;
}

.top-nav-container-right {
    display: flex;
    align-items: center;
}

ul {
    margin: 0;
    padding: 0;
    display: flex;
    list-style-type: none;
}

.nav-container a {
    padding: 0 20px;
    color: #292929;
}

.profile-picture-container {
    width: 32px;
    height: 32px;
    margin: 0 10px;
    object-fit: scale-down;
}

.cart-container {
    width: 32px;
    height: 32px;
    position: relative;
    margin: 0 10px;
    object-fit: scale-down;
}

.cart-counter {
    height: 16px;
    width: 16px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    font-size: 12px;
    color: white;
    font-weight: bold;
    background-color: red;
    position: absolute;
}

@media only screen and (max-width: 768px) {
    .top-nav-container {
        min-height: 56px;
    }

    .hamburger-container {
        display: initial;
        width: 36px;
        height: 36px;
        margin: 0 10px;
        object-fit: scale-down;
    }

    .logo-container {
        height: 40px;
        font-size: 28px;
    }

    .nav-container {
        display: none;
    }

    .profile-picture-container {
        display: none;
    }
    

    .cart-container {
        width: 36px;
        height: 36px;
    }
}