.sock-page-container {
    width: 1400px;
    max-width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: lightpink;
}

.sock-display {
    padding: 20px;
    max-width: 768px;
}

.sock-display img {
    max-width: 100%;
}

.sock-order-section-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.sock-order-section {
    padding: 30px 20px 50px;
    background-color: #fff;
}

.sock-page-name {
    margin: 10px;
    font-size: 24px;
    font-weight: bold;
}

.sock-page-description {
    font-size: 14px;
    color: #525252
}

.sock-page-price {
    margin: 30px 0 10px;
}

.wrong-size-alert {
    color: red;
}

.sock-details-title {
    padding: 20px 10px;
    background-color: #F0F0F0;
    text-align: end;
    font-weight: bold;
}

.sock-details {
    padding: 20px 10px;
    background-color: #F0F0F0;
    text-align: start;
}

@media only screen and (max-width: 768px) {
    .sock-page-container {
        grid-template-columns: 1fr;
    }

    .sock-details-title {
        text-align: center;
    }

    .sock-details {
        text-align: center;
        padding-top: 0;
    }

    .sock-details > p {
        margin-top: 0;
    }
}