.home-page-banner-container {
    height: 60vh;
    background:linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url("https://a.optmnstr.com/users/b087f2df60dd/images/76226086f74c1550256078-shutterstock-blog.jpg") no-repeat bottom center; /*https://i.imgur.com/5lmwwtU.gif*/
    background-size: cover;

    display: flex;
    vertical-align: middle;

    color: white;
    text-shadow: black 2px 2px;
}

.home-page-banner-container > span {
    margin: auto;
    display: flex;
    flex-direction: column;
    max-width: 800px;
}

.home-page-banner-title {
    font-family: 'Catamaran', sans-serif;
    font-weight: 900;
    font-size: 60px;
}

.home-page-banner-subtitle {
    font-style: italic;
    font-size: 30px;
    animation: color-change 3s infinite;
}

.home-page-banner-subtitle > a:link {
    color:white;
}

.home-page-banner-subtitle > a:active {
    color:white;
}

.home-page-banner-subtitle > a:visited {
    color:white;
}

@media only screen and (max-width: 768px){
    .home-page-banner-title {
        font-size: 38px;
    }
    .home-page-banner-subtitle {
        font-size: 20px;
    }
}

@keyframes color-change {
    0% { color: grey; }
    50% { color: rgb(255, 255, 255); }
    100% { color: grey; }
}