.sock-tile-container {
    background-color: #f5f5f5;
    height: fit-content;
    border-radius: 6px;
}

.sock-tile-container, a, a:link, a:visited{
    text-decoration: none;
    color: black;
}

.image-container {
    padding: 20px;
    box-sizing: border-box;
    height: 300px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #dbdbdb;
    border-radius: 6px 6px 0 0;
}

.image-container::before {
    /*content:"";*/
    height: 20px;
    width: 20px;
    display:block;
    position: absolute;
    background-color: red;
}

.sock-tile-container .image {
    width: 100%;
    max-height: 100%;
    object-fit: contain;
}

.sock-name {
    font-weight: bold;
}

.add-to-cart-button {
    padding: 10px;
    margin: 20px;
    background-color: orange;
}

@media only screen and (max-width: 768px) {
    .image-container {
        padding: 15px 12px;
        height: 250px;

    }
}