.shopping-page-cart-container{
    min-height: 600px;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.shopping-page-cart-receipt {
    padding: 30px 0;
}

.shopping-page-cart-total-price {
    padding: 20px 0;
}

.shopping-page-cart-payment {
    padding: 30px 0;
    background-color: #F0F0F0;
    display: flex;
    flex-direction: column;
    align-items: center;
    /*justify-content: center;*/
}

.shopping-page-cart-payment-title {
    margin-bottom: 20px;
    font-size: 30px;
}

.payment-methods {
    margin-bottom: 30px;
    display: flex;

    justify-content: center;
}

.payment-method {
    position: relative;
    margin: 10px;
    background-color: #fff;
    cursor: pointer;
    border-radius: 16px;
    height: fit-content;
}

.payment-method-image-container {
    padding: 20px;
    box-sizing: border-box;
    height: 100px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.selected-payment-method {
    box-sizing: border-box;
    /*border-bottom: 6px solid limegreen;*/
    box-shadow: 0 0 2px 2px limegreen;
    transition: 200ms;
}

.payment-method-overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    cursor: not-allowed;
    background-color: rgba(0,0,0,0.3);
}

.place-order-button {
    padding: 10px;
    border-radius: 6px;
    background-color: LIGHTGREY;
    border: 2px solid grey;
    color:grey;
    
}

.place-order-button-selected {
    background-color: green;
    border: 2px solid darkgreen;
    color: white;
    
    transition: 300ms;
    cursor: pointer;
}


@media only screen and (max-width: 768px) {
    .shopping-page-cart-container{
        grid-template-columns: 1fr;
    }
}